/*--------------------------------------------------------------
# PAges
--------------------------------------------------------------*/
.boomi-stats-notifications .header-row {
  text-align: center;
  margin-bottom: 1rem;
}
.boomi-stats-notifications .header-row h2 {
  background-color: #0087CC;
  padding: 15px;
  margin: 0;
  color: #fff;
}
.boomi-stats-notifications .data-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  width: 100%;
}
.boomi-stats-notifications .data-container .border-wrap {
  border-color: #0087CC;
}
.boomi-stats-notifications .data-container .sub-head {
  background-color: #0087CC;
}
.boomi-stats-notifications .border-wrap {
  overflow: hidden;
  border-style: solid;
  border-width: 1px;
}
.boomi-stats-notifications .sub-head {
  text-align: center;
  color: #fff;
}
.boomi-stats-notifications .sub-head h3 {
  margin: 0;
  padding: 15px;
}
.boomi-stats-notifications .date {
  padding: 10px;
}
.boomi-stats-notifications .date:nth-child(even) {
  background-color: #F7F7F7;
}
.boomi-stats-notifications .upcoming-releases,
.boomi-stats-notifications .infrastructure-releases,
.boomi-stats-notifications .release-archive {
  margin-bottom: 2rem;
}
.boomi-stats-notifications .infrastructure-releases h2 {
  background-color: #22C5BE;
}
.boomi-stats-notifications .infrastructure-releases .data-container .border-wrap {
  border-color: #22C5BE;
}
.boomi-stats-notifications .infrastructure-releases .data-container .sub-head {
  background-color: #22C5BE;
}
.boomi-stats-notifications .release-archive .release-archive-header h2 {
  background-color: #808080;
}
.boomi-stats-notifications .release-archive .data-container .border-wrap {
  border-color: #808080;
}
.boomi-stats-notifications .release-archive .data-container .sub-head {
  background-color: #808080;
}
.boomi-stats-notifications .footer-notice {
  margin-top: 1rem;
}